import { Search, ArrowUpDown } from 'lucide-react';
import { useState, useMemo } from 'react';
import type { Repository, SortField, SortDirection } from '@/types/repository';

interface RepositoryTableProps {
  repositories: Repository[];
}

const getStatusIcon = (status: Repository['status']) => {
  switch (status) {
    case 'ok':
      return '✅';
    case 'partial':
      return '⚠';
    case 'eol':
      return '❌';
    default:
      return '';
  }
};

const getStatusText = (status: Repository['status']) => {
  switch (status) {
    case 'ok':
      return 'OK';
    case 'partial':
      return 'Partial Support';
    case 'eol':
      return 'EOL';
    default:
      return '';
  }
};

export function RepositoryTable({ repositories }: RepositoryTableProps) {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortField, setSortField] = useState<SortField>('service');
  const [sortDirection, setSortDirection] = useState<SortDirection>('asc');

  const handleSort = (field: SortField) => {
    if (field === sortField) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  const filteredAndSortedRepos = useMemo(() => {
    return repositories
      .filter((repo) => {
        const searchLower = searchTerm.toLowerCase();
        return (
          repo.service.toLowerCase().includes(searchLower) ||
          repo.language.toLowerCase().includes(searchLower) ||
          repo.runtime.toLowerCase().includes(searchLower) ||
          repo.baseImage.toLowerCase().includes(searchLower) ||
          repo.keyLibraries.some((lib) => lib.toLowerCase().includes(searchLower))
        );
      })
      .sort((a, b) => {
        let compareA = a[sortField];
        let compareB = b[sortField];
        
        if (typeof compareA === 'string' && typeof compareB === 'string') {
          return sortDirection === 'asc'
            ? compareA.localeCompare(compareB)
            : compareB.localeCompare(compareA);
        }
        return 0;
      });
  }, [repositories, searchTerm, sortField, sortDirection]);

  return (
    <div className="space-y-4">
      <div className="flex items-center px-4 py-2 bg-white rounded-lg shadow">
        <Search className="w-5 h-5 text-gray-400 mr-2" />
        <input
          type="text"
          placeholder="Search repositories..."
          className="flex-1 outline-none"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>

      <div className="bg-white rounded-lg shadow overflow-x-auto">
        <table className="min-w-full">
          <thead>
            <tr className="bg-gray-50">
              {['Service', 'Language', 'Runtime', 'Base Image', 'Key Libraries', 'Status'].map((header, index) => (
                <th
                  key={header}
                  className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort(header.toLowerCase().replace(' ', '') as SortField)}
                >
                  <div className="flex items-center space-x-1">
                    <span>{header}</span>
                    <ArrowUpDown className="w-4 h-4" />
                  </div>
                </th>
              ))}
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredAndSortedRepos.map((repo) => (
              <tr key={repo.id} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">{repo.service}</td>
                <td className="px-6 py-4 whitespace-nowrap">{repo.language}</td>
                <td className="px-6 py-4 whitespace-nowrap">{repo.runtime}</td>
                <td className="px-6 py-4 whitespace-nowrap">{repo.baseImage}</td>
                <td className="px-6 py-4">
                  <div className="space-y-1">
                    {repo.keyLibraries.map((lib) => (
                      <span key={lib} className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800 mr-2">
                        {lib}
                      </span>
                    ))}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="flex items-center space-x-1">
                    <span>{getStatusIcon(repo.status)}</span>
                    <span>{getStatusText(repo.status)}</span>
                  </span>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
